<template>
  <div>
    <v-form ref="form" v-model="valid">
      <v-container>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="item.host" label="Host IPv4 Address" required :rules="[rules.required]"></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field type="number" v-model="item.port" label="Host Port Address" :rules="[rules.required]" required></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-switch v-model="item.type_udp" label="Host Port Of Type UDP"></v-switch>
          </v-col>
          <v-col cols="12" md="2" class="d-flex" style="align-items: center">
            <v-btn @click="check" color="primary" small> PING </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <v-container>
      <vue-json-pretty
        :itemHeight="1000"
        :virtual="true"
        style="text-align: left"
        :data="result || 'Hit PING to see if a remote port is open'"
      >
      </vue-json-pretty>
    </v-container>
    <v-overlay :value="loading"></v-overlay>
    <v-dialog v-model="loading" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text style="padding: 5px">
          Loading
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
          <v-btn x-small color="darken-1" text @click="loading = false">Cancel</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from '../../axios';
  import VueJsonPretty from 'vue-json-pretty';
  export default {
    components: {
      VueJsonPretty
    },
    data: () => ({
      tab: 0,
      valid: true,
      loading: false,
      result: null,
      rules: { required: (value) => !!value || "Required." },
      item: {
        host: "192.168.15.10",
        port: 22
      }
    }),
    methods: {
      check() {
        if (this.$refs.form.validate()) {
          this.item.type_udp = !!this.item.type_udp;
          this.item.port = Number(this.item.port);
          this.loading = true;
          axios.post('rubix-service/networking/check_port', this.item).then(r => {
            let msg = null;
            if(r == true){
             msg = `${this.item.host}:${this.item.port} is open`
            } else {
              msg = `${this.item.host}:${this.item.port}  is closed`
            }
            this.$toasted.show(msg);
            this.result = msg;
            this.loading = false;
          })
        }
      }
    }
  };
</script>
