<template>
  <div>
    <v-form ref="form" v-model="valid">
      <v-container>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field v-model="item.address" label="Address" required :rules="[rules.required]"></v-text-field>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field type="number" v-model="item.start_address" label="Start Address" :rules="[rules.required]" required></v-text-field>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field type="number" v-model="item.end_address" label="End Address" :rules="[rules.required]" required></v-text-field>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field type="number" v-model="item.timeout_seconds" label="Timeout (seconds)" :rules="[rules.required]" required></v-text-field>
          </v-col>
          <v-col cols="12" md="3" class="d-flex" style="align-items: center">
            <v-btn @click="check" color="primary" small> Scan </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <v-container>
      <!-- <vue-json-pretty :itemHeight="1000" :virtual="true" style="text-align: left" :data="result || 'Hit Send to get a response'" > </vue-json-pretty> -->
      <v-data-table v-if="result" :headers="headers" :items="items" item-key="index" sort-by="ip" disable-sort group-by="category" class="elevation-1 text-left" show-group-by >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Status</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <div class="text-left pa-2">
              <!-- Export btn -->
              <v-btn @click="clickJsonExport()" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>
            </div>
          </v-toolbar>
        </template>


        <template v-slot:group.header="{ group, headers, toggle, isOpen, items }">
          <td :colspan="headers.length" >
            <v-btn @click="toggle" x-small icon :ref="group">
                <v-icon>{{ isOpen ? 'mdi-menu-down' : 'mdi-menu-right' }}</v-icon>
            </v-btn>
            <span class="mx-5 font-weight-bold">{{ group }}</span>
          </td>
        </template>
        <template v-slot:item="{ item }">
          <tr :class="`${item.status ? 'background-status-true' : 'background-status-false'}`" >
            <td>{{ item.ip }}</td>
            <td>{{ item.status }}</td>
          </tr>
        </template>
      </v-data-table>
      <div v-else class="text-left" style="color: #13ce66;"> <span>"Hit SCAN to do an IP scan"</span></div>
    </v-container>
    <v-overlay :value="loading"></v-overlay>
    <v-dialog v-model="loading" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text style="padding: 5px">
          Loading
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
          <v-btn x-small color="darken-1" text @click="loading = false">Cancel</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from '../../axios';
  import VueJsonPretty from 'vue-json-pretty';
  import { toUpperTextKey, copyToClipboard } from '../../utils/utils';

  export default {
    components: {
      VueJsonPretty
    },
    data: () => ({
      tab: 0,
      valid: true,
      loading: false,
      result: null,
      rules: { required: (value) => !!value || "Required." },
      item: {
        address: "192.168.15",
        start_address: 1,
        end_address: 5,
        timeout_seconds: 2
      },
      headers: [
        { text: 'IP', align: 'start', value: 'ip', groupable: false, },
        { text: 'Status', align: 'start', value: 'status', groupable: false, },
        { text: 'Category', value: 'category', align: 'right' },
      ],
      items: [],
    }),
    methods: {
      check() {
        if (this.$refs.form.validate()) {
          this.item.start_address = Number(this.item.start_address);
          this.item.end_address = String(this.item.end_address);
          this.item.timeout_seconds = Number(this.item.timeout_seconds);
          this.loading = true;
          axios.post('rubix-service/networking/ping_range', this.item).then(r => {
            this.result = {};
            this.items = [];
            this.result = r;
            Object.keys(this.result).forEach(key=>{
              if(this.result[key].length > 0){
                this.result[key].forEach(host=>{
                  host = {...host, category: toUpperTextKey(key),};
                  this.items.push(host);
                });
              }
            });
            this.loading = false;
          })
        }
      },
      clickJsonExport() {
        const status = copyToClipboard(JSON.stringify(this.result));
        const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, { type: type });
      },
    }
  };
</script>
<style>
  .background-status-true{
    color: #0dec67;
  }
  .background-status-false{
    color: #fbb93e;
  }
</style>
